/* You can add global styles to this file, and also import other style files */
@import url("https://fonts.googleapis.com/css?family=Quicksand:300,400,600");
@import url("https://fonts.googleapis.com/css2?family=Arvo&display=swap");
@import url("https://fonts.googleapis.com/css?family=Lato");
@import url("//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css");
html, body, p {
  font-size: 14px;
  font-family: "Quicksand", sans-serif;
}

p {
  font-size: 20px;
  letter-spacing: 2px;
}

h1 {
  font-family: "Arvo", serif;
}

.big-link {
  font-family: "Arvo", serif;
  font-size: 40px;
  text-decoration: none;
  display: block;
  color: black;
  width: fit-content;
  position: relative;
  text-align: center;
  padding: 0 10px 0 10px;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 25px;
  transition: all 0.2s ease-in-out;
}
.big-link .streak {
  width: calc(98% + 10px);
  height: 15px;
  position: absolute;
  z-index: -1;
  top: 30px;
  left: -5px;
}
.big-link .streak.blue {
  background-color: #99c6d9;
}
.big-link .streak.mauve {
  background-color: #c39aa5;
}
.big-link .streak.orange {
  background-color: #c79357;
}
.big-link:hover {
  cursor: pointer;
  transform: scale(1.25, 1.25);
}